.contact_main {
  text-align: center;
  padding-top: 4rem;
  margin: auto;
  margin-top: 8rem;
}
.contact_flex {
  display: flex;
  width: 70vw;
  justify-content: space-between;
  padding-top: 4rem;
  margin: auto;
}
.con_img {
  display: flex;
  justify-content: center;
}
.con_img a {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 6px;
  padding: 6px;
  color: white;
  height: 30px;
}
.con_img a:hover {
  background-color: rgba(255, 255, 255, 0.075);
  color: var(--crimson);
}
.con_img a p {
  margin-top: -3px;
}
.con_img p:hover {
  background-color: rgba(255, 255, 255, 0.075);
  color: var(--crimson);
}
.git_con {
  display: flex;
  text-decoration: none;
  gap: 4px;
  justify-content: space-around;
  background-color: var(--black);
}
.git_con img {
  width: 30px;
}
.linkdin {
  background-color: #0084ff94;
  display: flex;
  text-decoration: none;
  gap: 10px;
}
.linkdin img {
  width: 30px;
}
.gmail {
  background-color: #eb4134;
}
.contact-form {
  display: flex;
}
.massge_img {
  height: 300px;
  opacity: 0.8;
}
.c-right {
  justify-content: center;
  position: relative;
  flex: 1;
}
.c-right div {
  gap: 8px;
  margin: auto;
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  text-align: left;
}
.get_touch {
  font-size: 2rem;
  font-weight: bold;
  color: var(--black);
}
.contact {
  font-size: 2.5rem;
  font-weight: bold;
  color: var(--crimson);
}
.c-right .user {
  height: 2rem;
  padding: 0.3em;
  outline: none;
  border: 1px solid var(--crimson);
  border-radius: 4px;
  font-size: 16px;
  margin: auto;
  background-color: #eeeeee00;
}
.c-right .user:focus {
  background-color: #bababa92;
}

textarea {
  height: 9rem !important;
  width: 90%;
}

.c-blur1 {
  top: 1rem;
  left: 8rem;
}
.button_su {
  background-color: var(--crimson);
  font-size: 22px;
  border: none;
  color: white;
  border-radius: 4px;
  padding: 3px;
  width: 200px;
  cursor: pointer;
  font-weight: 600;
  margin: auto;
  border: 1px solid var(--crimson);
}
.button_su:hover {
  border: 1px solid var(--crimson);
  background-color: rgba(5, 5, 5, 0);
  color: var(--crimson);
}

@media screen and (max-width: 1000px) {
  .contact_flex {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: -80px;
    width: 80%;
  }
  .heding_cont {
    text-align: center;
    padding: 10px;
  }
  .c-right {
    display: grid;
    justify-content: center;
    margin-top: 4rem;
  }
}
